___CSS_LOADER_EXPORT___.push([module.id, `body,html{
    margin:0;
    padding:0;
}

body{
    background-color: #C4DBE0 ;
    color: white;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.header{
    font-size: 80px;
    font-weight: bold;
    text-shadow: 4px 4px 4px #002c66;
    display: flex;
    justify-content: center;
}
.boards{
    display: flex;
    gap: 50px;
    justify-content: center;
    margin-top: 50px;
}

div[class^="column"]{
    border: 2px solid grey;
}
[class^="row"]{
    display: grid;
    grid-template-columns: repeat(10,1fr);
}
.playerBoard, .computerBoard{
    height: 400px;
    width: 400px;
    background-color: #002c66;
    border: 2px solid grey;
    display: grid;
    grid-template-rows: repeat(10,1fr);
}

.playerBoard .ship{
    background-color: rgb(175, 210, 255);
}

.boards .attacked{
    background-color: rgba(255, 0, 0, 0.445);
}
.boards .attacked.ship{
    background-color: rgb(37, 247, 9);
}

.display{
    height: 30px;
    width: 150px;
    color: rgb(228, 0, 0);
    text-align: center;
    text-shadow: 3px 3px 3px rgba(0, 0, 255, 0.377);
    font-size: 23px;
    font-weight: 700;
    font-style:italic;
    
    align-self: center;

    padding: 20px 10px;
    padding-bottom: 20px;
    border-bottom: 3px solid white;
    border-top: 3px solid white;
}

dialog:not([open]) {
    display: none;
  }

dialog[open]{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:-40px;
}

button{
    width: 90px;
    height: 40px;
    font-size: 15px;
    font-weight: bold;
    background-color: #002c66;
    color:white;
    border-radius: 10px;
    outline: none;
    box-shadow: 1px 1px 1px blue;
}

dialog[open] .boards{
    margin-top:20px;
}
button:hover{
    box-shadow: 3px 3px 3px 3px #002c66;
}

dialog[open] .heading{
    font-size: 30px;
    font-weight: bold;
    padding-bottom: 20px;
}

dialog[open] .message{
    font-size: 20px;
    padding-bottom: 10px;
}

#new-game{
    width: 200px;
    height: 150px;
    position: absolute;
    top: 50px;
}
#newBtn{
    font-size: 25px;
    width: 150px;
    height: 70px;
    border-radius: 0px;
    box-shadow: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/mainStyle.css"],"names":[],"mappings":"AAAA;IACI,QAAQ;IACR,SAAS;AACb;;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,kJAAkJ;;AAEtJ;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,gCAAgC;IAChC,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,qCAAqC;AACzC;AACA;IACI,aAAa;IACb,YAAY;IACZ,yBAAyB;IACzB,sBAAsB;IACtB,aAAa;IACb,kCAAkC;AACtC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,wCAAwC;AAC5C;AACA;IACI,iCAAiC;AACrC;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,qBAAqB;IACrB,kBAAkB;IAClB,+CAA+C;IAC/C,eAAe;IACf,gBAAgB;IAChB,iBAAiB;;IAEjB,kBAAkB;;IAElB,kBAAkB;IAClB,oBAAoB;IACpB,8BAA8B;IAC9B,2BAA2B;AAC/B;;AAEA;IACI,aAAa;EACf;;AAEF;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,yBAAyB;IACzB,WAAW;IACX,mBAAmB;IACnB,aAAa;IACb,4BAA4B;AAChC;;AAEA;IACI,eAAe;AACnB;AACA;IACI,mCAAmC;AACvC;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,eAAe;IACf,oBAAoB;AACxB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,SAAS;AACb;AACA;IACI,eAAe;IACf,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;AACpB","sourcesContent":["body,html{\n    margin:0;\n    padding:0;\n}\n\nbody{\n    background-color: #C4DBE0 ;\n    color: white;\n    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n\n}\n\n.header{\n    font-size: 80px;\n    font-weight: bold;\n    text-shadow: 4px 4px 4px #002c66;\n    display: flex;\n    justify-content: center;\n}\n.boards{\n    display: flex;\n    gap: 50px;\n    justify-content: center;\n    margin-top: 50px;\n}\n\ndiv[class^=\"column\"]{\n    border: 2px solid grey;\n}\n[class^=\"row\"]{\n    display: grid;\n    grid-template-columns: repeat(10,1fr);\n}\n.playerBoard, .computerBoard{\n    height: 400px;\n    width: 400px;\n    background-color: #002c66;\n    border: 2px solid grey;\n    display: grid;\n    grid-template-rows: repeat(10,1fr);\n}\n\n.playe