export { DomManipulation, dqs, createDiv, placeShipRandomly };

/* eslint-disable no-use-before-define */
function placeShipRandomly(board, length) {
  const xPos = Math.floor(Math.random() * 10);
  const yPos = Math.floor(Math.random() * 10);
  const axis = ["X", "Y"][Math.floor(Math.random() * 2)];

  let success = board.placeShip(xPos, yPos, axis, length);
  while (!success) {
    success = board.placeShip(xPos, yPos, axis, length);
  }
}

function createDiv(className) {
  const div = document.createElement("div");
  div.setAttribute("class", `${className}`);
  return div;
}

function dqs(selector) {
  return document.querySelector(selector);
}

function Axis() {
  const xBtn = dqs("#axis");
  let axisVal = "X";
  xBtn.addEventListener("click", () => {
    // eslint-disable-next-line no-unused-expressions
    axisVal === "X" ? (axisVal = "Y") : (axisVal = "X");
  });
  function getAxis() {
    return axisVal;
  }
  return { getAxis };
}

function onMouseOver(boardEle, playerBoard, dialog) {
  let pos = [];
  const ships = [3, 2, 3, 4, 5];
  const boardAxis = Axis();
  let shipSize = ships[0];
  let axis = boardAxis.getAxis();

  boardEle.addEventListener("mousemove", (event) => {
    shipSize = ships[0];
    axis = boardAxis.getAxis();

    boardEle.replaceChildren();
    DomManipulation.playerBoardRenderer(playerBoard.board, boardEle);
    const cell = event.target;
    if (!cell.classList.contains("playerBoard")) {
      pos = DomManipulation.getPos(cell);
      const [xPos, yPos] = pos;
      if (axis === "X") {
        for (let i = yPos; i < yPos + shipSize; i += 1) {
          if (i >= 0 && i <= 9) {
            const row = boardEle.querySelector(`.row_${xPos}`);
            const column = row.querySelector(`.column_${i}`);
            column.style.backgroundColor = "aqua";
          }
        }
      } else if (axis === "Y") {
        for (let i = xPos; i < xPos + shipSize; i += 1) {
          if (i >= 0 && i <= 9) {
            const row = boardEle.querySelector(`.row_${i}`);
            const column = row.querySelector(`.column_${yPos}`);
            column.style.backgroundColor = "aqua";
          }
        }
      }
    }
  });

  boardEle.addEventListener("mousedown", () => {
    const [xPos, yPos] = pos;
    const success = playerBoard.placeShip(xPos, yPos, axis, shipSize);
    if (success) {
      ships.shift();
    }
    boardEle.replaceChildren();
    DomManipulation.playerBoardRenderer(playerBoard.board, boardEle);

    if (ships.length === 0) {
      dialog.close();
    }
  });
}

class DomManipulation {
  static playerBoardRenderer(board, parentElement) {
    board.forEach((element, rowIndex) => {
      const row = createDiv(`row_${rowIndex}`);

      element.forEach((cell, columnIndex) => {
        const column = createDiv(`column_${columnIndex}`);
        if (cell === 11) {
          column.classList.add("attacked");
        } else if (cell === 10) {
          column.classList.add("ship", "attacked");
        } else if (cell !== 0 && cell !== "X") {
          column.classList.add("ship");
        }
        row.appendChild(column);
      });
      parentElement.appendChild(row);
    });
  }

  static opponentBoardRenderer(board, parentElement) {
    board.forEach((element, rowIndex) => {
      const row = createDiv(`row_${rowIndex}`);

      element.forEach((cell, columnIndex) => {
        const column = createDiv(`column_${columnIndex}`);
        if (cell === 11) column.classList.add("attacked");
        if (cell === 10) column.classList.add("ship", "attacked");
        row.appendChild(column);
      });
      parentElement.appendChild(row);
    });
  }

  static getPos(element) {
    const yPos = +Array.from(element.classList)
      .filter((className) => className.startsWith("column"))[0]
      .split("_")[1];
    const xPos = +Array.from(element.parentElement.classList)
      .filter((className) => className.startsWith("row"))[0]
      .split("_")[1];
    return [xPos, yPos];
  }

  static dialogBoxCreator(playerBoard, dialog) {
    dialog.showModal();
    const boardEle = dqs("#dialog-box .boards .playerBoard");
    DomManipulation.playerBoardRenderer(playerBoard.board, boardEle);
    onMouseOver(boardEle, playerBoard, dialog);
  }
}
